@font-face {
  font-family: "Roboto";
  src: url("../fonts/roboto.woff2") format("woff2"), url("../fonts/roboto.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "Roboto";
  src: url("../fonts/robotomedium.woff2") format("woff2"), url("../fonts/robotomedium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "Roboto";
  src: url("../fonts/robotobold.woff2") format("woff2"), url("../fonts/robotobold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap; }

html {
  box-sizing: border-box; }

*,
*::before,
*::after {
  box-sizing: inherit; }

* {
  margin: 0;
  padding: 0; }

body {
  position: relative;
  margin: 0 auto;
  width: 1440px;
  font-family: "Roboto", "Arial", sans-serif;
  font-weight: normal;
  font-size: 16px;
  line-height: 24px;
  color: #283136;
  background-color: #ffffff; }

a {
  text-decoration: none; }

.wrapper {
  width: 1200px;
  margin: 0 auto;
  padding: 0 20px; }

.overflow {
  overflow: hidden; }

.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0, 0, 0, 0);
  overflow: hidden; }

.main-header {
  background-color: #eeeeee; }

.main-navigation {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%; }

.main-navigation__menu {
  padding: 0;
  margin: 0;
  list-style: none;
  display: flex;
  justify-content: center;
  width: 100%; }

.main-navigation__item {
  margin-right: 50px; }

.main-navigation__link {
  font-size: 16px;
  line-height: 30px;
  font-weight: 500;
  color: #000000;
  text-transform: uppercase; }
  .main-navigation__link:hover, .main-navigation__link:focus {
    color: #fb5559; }
  .main-navigation__link:active {
    color: #000000;
    opacity: 0.3; }
  .main-navigation__link--active {
    border-bottom: 2px solid #fb5559; }

.main-navigation__user {
  padding: 0;
  margin: 0;
  list-style: none; }

.promo {
  background-color: #eeeeee; }

.promo__slider {
  position: relative;
  width: inherit;
  height: 430px; }

.slider input[type="radio"] {
  display: none; }

.slider__header {
  width: 560px; }

.slider__title {
  font-size: 55px;
  line-height: 55px;
  font-weight: 500;
  color: #000000; }

.slide {
  position: absolute;
  width: inherit;
  height: inherit;
  top: 0;
  left: 0;
  z-index: 0;
  opacity: 0;
  background-repeat: no-repeat;
  background-size: contain;
  background-position: top right; }

.slide-1 {
  background-image: url("../img/bg-slide-01.png"); }

.slide-2 {
  background-image: url("../img/bg-slide-02.png"); }

.slide-3 {
  background-image: url("../img/bg-slide-03.png"); }

.slider__control {
  position: absolute;
  bottom: 10%;
  left: 50%;
  z-index: 10; }
  .slider__control label {
    position: relative;
    margin-right: 20px;
    font-size: 0;
    display: inline-block;
    width: 18px;
    height: 18px;
    border-radius: 50%;
    background-color: #ffffff; }

#slide-1:checked ~ .slide-1,
#slide-2:checked ~ .slide-2,
#slide-3:checked ~ .slide-3 {
  opacity: 1;
  z-index: 5; }

#slide-1:checked ~ .slider__control label:nth-child(1)::after,
#slide-2:checked ~ .slider__control label:nth-child(2)::after,
#slide-3:checked ~ .slider__control label:nth-child(3)::after {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  width: 2px;
  height: 2px;
  border: 1px solid #000000;
  border-radius: 50%; }

.btn {
  padding: 15px 50px;
  font-size: 16px;
  line-height: 18px;
  font-weight: 500;
  color: #ffffff;
  text-align: center;
  text-transform: uppercase;
  background-color: #fb5559; }
  .btn:hover, .btn:focus {
    background-color: #e74246; }
  .btn:active {
    color: #e37375;
    background-color: #d7363a;
    box-shadow: inset 0 3px 0 0 #c13134; }
